import { state } from "./state";

export function fetchDrinksByName() {
    //const loader = document.getElementById(`loading`);
    //loader.style.visibility = `visible`;
    const url = `https://www.thecocktaildb.com/api/json/v1/1/search.php?s=${state.searchTerm}`;
    return fetch(url)
        .then(res => res.json())
        .then(data => {
            //loader.style.visibility = `hidden`;
            return data.drinks;
        })
        .catch(error => console.error(error));
}

export function fetchDrinksByIngredient() {
    //const loader = document.getElementById(`loading`);
    //loader.style.visibility = `visible`;
    const url = `https://www.thecocktaildb.com/api/json/v1/1/filter.php?i=${state.searchTerm}`;
    return fetch(url)
        .then(res => res.json())
        .then(data => {
            //loader.style.visibility = `hidden`;
            return data.drinks;
        })
        .catch(error => {
            console.error(error);
            return error;
        });
}

export function fetchDrinkDetailsByID(id) {
    //const loader = document.getElementById(`loading`);
    //loader.style.visibility = `visible`;
    const url = `https://www.thecocktaildb.com/api/json/v1/1/lookup.php?i=${id}`;
    return fetch(url)
        .then(res => res.json())
        .then(data => {
            //loader.style.visibility = `hidden`;
            return data;
        })
        .catch(error => console.error(error));
}